@import '../utils/variables';
@import '../utils/mixins';

.cta {
  width: 218px;
  height: 50px;
  border-radius: 25px;
  border: none;
  color: $white;
  font-size: 16px;
  font-weight: 500;
  background: linear-gradient(0deg, $primary-color 0%, $secondary-color 100%);
  @include button-shadow;
  cursor: pointer;
}
